import React from "react";
import { idadeModernaText } from "../../resources/texts";
import belem from "../../resources/images/belem.jpg";
import descobrimento from "../../resources/images/descobrimento.jpg";

export const IIIPageTwo = props => {

    return (
        <>
            <h1 className='stageTitle'>Lisboa</h1>
            <section className='grid2x2'>
                <p className='stageParagraph rightStack'>{idadeModernaText.pageTwo}</p>
                <figure className='stageFigure iTopLeft'>
                    <img src={belem}/>
                    <figcaption>Torre de Belem, Lisboa</figcaption>
                </figure>
                <figure className='stageFigure iBottomLeft'>
                    <img src={descobrimento}/>
                    <figcaption>Monumento do Descobrimento, Lisboa</figcaption>
                </figure>
            </section>

        </>
    );
};