import React from "react";
import { revIndustrialText } from "../../resources/texts";
import locomotiva from "../../resources/images/locomotiva.jpg";
import johnlocke from "../../resources/images/johnlocke.jpg";


export const IVPageOne = props => {

    return (
        <>
            <h1 className='stageTitle'>IV. Revolução Industrial</h1>
            <section className='grid2x2'> 
                <p className='stageParagraph pTopLeft'>{revIndustrialText.pageOne1}</p>
                <figure className='stageFigure iBottomLeft'>
                    <img src={locomotiva}/>
                    <figcaption>Locomotiva a vapor</figcaption>
                </figure>
                <p className='stageParagraph pBottomRight'>{revIndustrialText.pageOne2}</p>
                <figure className='stageFigure iTopRight'>
                    <img src={johnlocke}/>
                    <figcaption>John Locke</figcaption>
                </figure>
            </section>
        </>
    );
};