import React from "react";
import { secXXText } from "../../resources/texts";
import primeiraguerra from "../../resources/images/primeiraguerra.jpg";
import depression from "../../resources/images/depression.jpg";


export const VPageOne = props => {

    return (
        <>
            <h1 className='stageTitle'>V. Início do Século XX</h1>
            <section className='grid2x2'>
                <p className='stageParagraph leftStack' >{secXXText.pageOne}</p>
                <figure className='stageFigure iTopRight'>
                    <img src={primeiraguerra}/>
                    <figcaption>Batalha de Paschendale, Primeira Guerra</figcaption>
                </figure>
                <figure className='stageFigure iBottomRight'>
                    <img src={depression}/>
                    <figcaption>Fila de distribuição de alimentos, Grande Depressão</figcaption>
                </figure>
            </section>
        </>
    );
};