import React from "react";
import { revIndustrialText } from "../../resources/texts";
import industrias from "../../resources/images/industrias.jpg";
import bigben from "../../resources/images/bigben.png";

export const IVPageTwo = props => {

    return (
        <>
            <h1 className='stageTitle'>Londres</h1>
            <section className='grid2x2'>
                <p className='stageParagraph rightStack' >{revIndustrialText.pageTwo}</p>
                <figure className='stageFigure iTopLeft'>
                    <img src={industrias}/>
                    <figcaption>Industrias em Londres</figcaption>
                </figure>
                <figure className='stageFigure iBottomLeft'>
                    <img src={bigben}/>
                    <figcaption>Big Ben</figcaption>
                </figure>
            </section>
        </>
    );
}